<template>
  <div class="container-turnFront">
    <headerUsually btnShow="true" titleText="转AR" url="/mobileOrdering/pay"></headerUsually>
    <consumptionDetails @emitConsumptionDetails="consumptionDetailsFun($event)"></consumptionDetails>
    <div class="subNav">
      <div class="search-content">
        <input type="text" class="search" v-model="roomNum">
        <i class="iconfont icon-sousuo"></i>
      </div>
      <div class="btn" @click="searchRoomFun()">确定</div>
    </div>
    <div class="container-main" v-if="isSearchResult">
      <div class="main-nav" v-if="this.useList.length>0">
        <div>账号</div>
        <div>AR账号</div>
        <div>姓名</div>
        <div>余额</div>
        <div>状态</div>
        <div>记账</div>
      </div>
      <div class="main-content">
        <div class="list" v-if="this.useList.length>0">
          <div
            class="item"
            :class="activeItem==index?'activeItem':''"
            v-for="(item,index) in useList"
            :key="index"
            @click="selectFun(item)"
          >
            <div>{{item.accnt}}</div>
            <div>{{item.arNo}}</div>
            <div>{{item.name}}</div>
            <div>{{item.amount}}</div>
            <div>{{item.sta}}</div>
            <div>{{item.postingFlag}}</div>

            <div class="icon">
              <i v-show="activeItem==index" class="iconfont icon-xuanzhong"></i>
            </div>
          </div>
        </div>
        <div class="no-list" v-else>
          <noResult colors="rgba(57, 60, 65, 0.8)"></noResult>
        </div>
      </div>
    </div>
    <div v-else-if="!firstComein && isSearchResult">
      <income :obj="obj" @emitOK="emitOKFun($event)"></income>
    </div>
  </div>
</template>
<script>
import ajaxGet from "@/conf/ajaxGet";
import { Toast, MessageBox } from "mint-ui";
import { mapState, mapGetters } from "vuex";
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
import consumptionDetails from "@/components/common/mobileOrdering/consumptionDetails";
import income from "@/components/common/mobileOrdering/income";
import noResult from "@/components/common/mobileOrdering/noResult.vue";
export default {
  components: {
    headerUsually,
    consumptionDetails,
    income,
    noResult
  },
  data() {
    return {
      details: [],
      roomNum: "",
      useList: [],
      payCode: "",
      activeItem: -1,
      isSearchResult: false,
      firstComein: true,
      obj: { text: "aa", value: "11", needPayAmounts: 0 },
      selectItem: {}
    };
  },
  computed: {
    ...mapState({
      selectedTableInfo: state => state.mobileOrdering.selectedTableInfo
    })
  },
  async created() {
    this.payCode = this.$route.params.payCode;
  },
  methods: {
    consumptionDetailsFun(val) {
      this.obj.needPayAmounts = val;
    },
    async searchRoomFun() {
      if (this.roomNum) {
        let data = {
          params: {
            keyInput: this.roomNum
          }
        };
        let res = await ajaxGet("/pos/router?method=getAr", data);
        if (res && res.resultCode == 0) {
          this.useList = res.data;
          this.isSearchResult = true;
          this.firstComein = false;
        }
      }
    },
    selectFun(item) {
      this.obj = {
        text: "AR账号",
        value: item.arNo,
        needPayAmounts: this.obj.needPayAmounts
      };
      this.selectItem = item;
      this.isSearchResult = false;
    },
    async emitOKFun(item) {
      let coi = "";
      if (item.income < this.obj.needPayAmounts) {
        coi = 1;
      } else {
        coi = 0;
      }

      let data = {
        params: {
          accnt: this.selectedTableInfo.accnt,
          payCode: this.payCode,
          coi: coi,
          charge: item.income,
          cardNo: this.selectItem.accnt,
          remark: item.remark
        }
      };
      let res = await ajaxGet("/pos/router?method=updateCheckOut", data);
      if (res && res.resultCode == 0) {
        this.msg("支付成功！");
        if (res.data && res.data.billNo) {
          this.$router.push("/mobileOrdering/index");
        } else {
          this.$router.push("/mobileOrdering/pay");
        }
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  }
};
</script>
<style lang="less" scoped>
.container-turnFront {
  padding-top: 46px;
  .subNav {
    height: 60px;
    display: flex;
    align-items: center;
    padding: 0 15px;
    background-color: #fff;

    .search-content {
      flex: 1;
      border: 1px solid #ddd;
      border-radius: 5px;
      display: flex;
      align-items: center;
      height: 32px;
      padding: 0 13px;
      .search {
        flex: 1;
        outline: none;
        border: none;
      }
      .iconfont {
        font-size: 14px;
      }
    }
    .btn {
      margin-left: 10px;
      width: 75px;
      height: 32px;
      line-height: 32px;
      text-align: center;
      color: #fff;
      font-size: 14px;
      background: #c6af6c;
      border-radius: 5px;
    }
  }
  .container-main {
    margin-top: 10px;
    background-color: #fff;
    .main-nav {
      padding-left: 18px;
      display: flex;
      align-items: center;
      height: 50px;
      border-bottom: 1px solid #ddd;
      div {
        width: 16%;
        text-align: center;
      }
    }
    .main-content {
      position: absolute;
      top: 302px;
      bottom: 0;
      overflow-y: auto;
      width: 100%;
      .list {
        padding-left: 18px;
        background-color: #fff;
        .item {
          font-size: 14px;
          color: #666666;
          height: 60px;
          display: flex;
          align-items: center;
          justify-content: space-between;
          border-bottom: 1px solid #ddd;

          div {
            width: 16%;
            text-align: center;
          }
          .icon {
            width: 20px;
          }
        }
        .activeItem {
          color: #c6af6c;
        }
      }
    }
  }
  .content-details {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding: 10px 18px;
    .details {
      font-size: 14px;
      color: #666666;
      line-height: 36px;
      width: 50%;
      text-align: left;
    }
  }
}
</style>



