<template>
  <div class="receipt-container">
    <div class="list">
      <div class="item quantity">
        <div class="left">{{obj.text}}</div>
        <div class="right">{{obj.value}}</div>
      </div>
      <div class="item quantity">
        <div class="left">实收</div>
        <div class="right">
          <input type="text" v-model="income" />
        </div>
      </div>
      <div class="item remarks">
        <div class="left">备注</div>
        <div class="right">
          <input type="text" v-model="remark" />
        </div>
      </div>
    </div>
    <div class="btn" @click="okFun()">确定</div>
  </div>
</template>
<script>
import { Toast, MessageBox } from "mint-ui";
export default {
  props: {
    obj: Object
  },
  data() {
    return {
      income: "",
      remark: ""
    };
  },

  methods: {
    okFun() {
      if (this.income == "") {
        this.msg("请输入支付金额！");
        return;
      }
      let incomeNum = Number(this.income);
      if (this.obj.balance && incomeNum > Number.parseFloat(this.obj.balance)) {
        this.msg("支付金额大于卡内余额！");
        return;
      }
      if (incomeNum < 0) {
        this.msg("支付金额必须为非负数！");
        return;
      }
      if (incomeNum > this.obj.needPayAmounts) {
        this.msg("支付过多！");
        return;
      }
      this.$emit("emitOK", {
        income: incomeNum,
        remark: this.remark
      });
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  },
  watch: {
    income(val) {
      //console.log("val:", val);
      this.income = this.income.replace(/[^\d.]/g, ""); //清除“数字”和“.”以外的字符
      this.income = this.income.replace(/\.{2,}/g, "."); //只保留第一个. 清除多余的
      this.income = this.income
        .replace(".", "$#$")
        .replace(/\./g, "")
        .replace("$#$", ".");
      this.income = this.income.replace(/^(\-)*(\d+)\.(\d\d).*$/, "$1$2.$3"); //只能输入两个小数
      if (this.income.indexOf(".") < 0 && this.income != "") {
        //以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额
        this.income = parseFloat(this.income).toString();
      }
      //console.log("income:", this.income);
    }
  }
};
</script>
<style lang="less" scoped>
.receipt-container {
  .list {
    padding-left: 18px;
    background-color: #fff;
    .item {
      height: 65px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid #ddd;
      padding-right: 17px;
      .left {
        font-size: 14px;
        color: #666666;
      }
      .right {
        // text-align: right;
        // width: 70%;
        display: flex;
        align-items: center;
        font-size: 14px;
        color: #333333;
        input {
          text-align: right;
          outline: none;
          border: none;
          width: 100%;
        }
        .iconfont {
          color: #a8acb8;
        }
      }
    }
    .item:last-child {
      border-bottom: none;
    }
    .quantity {
      .right {
        .iconfont {
          color: #c4ad68;
          font-size: 30px;
        }
        .num {
          margin: 0 20px;
        }
      }
    }
  }
  .btn {
    margin-top: 20px;
    background: #ffffff;
    font-size: 18px;
    color: #c4ad68;
    text-align: center;
    padding: 11px;
  }
}
</style>



